import { lazy, Suspense, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';
import { HashRouter, Route, Routes } from 'react-router-dom';
import { RootState } from 'store';
import 'styles/index.scss';
import { MENU } from 'utils/constants';
import './App.scss';

const Home = lazy(() => import('./pages/Home'));
const Brand = lazy(() => import('./pages/Brands'));
const Partner = lazy(() => import('./pages/Partners'));
const About = lazy(() => import('./pages/About'));

function App() {
  const { i18n } = useTranslation();
  const lang = useSelector((state: RootState) => state.i18.lang);

  useEffect(() => {
    i18n.changeLanguage(lang);
  }, [lang]);

  return (
    <div className="App">
      <HashRouter>
        <Suspense fallback={<div>Loading...</div>}>
          <Routes>
            <Route path={MENU[0].path} element={<Home />} />
            <Route path={MENU[1].path} element={<Brand />} />
            <Route path={MENU[2].path} element={<Partner />} />
            <Route path={MENU[4].path} element={<About />} />
          </Routes>
        </Suspense>
      </HashRouter>
    </div>
  );
}

export default App;
